import React from 'react';
import './Styles/Profile.css'
import Card from './Card.js';

export default function Profile() {
  
    return (
        <section className="section-two" id="two-section" >
          <h2>Perfil Profesional</h2>
          <hr/>

          <div className="container-profile">

            <Card className="card-pitch" >
              <p>Soy Víctor Raul Huaycho Quispe, estudio la carrera de diseño y desarrollo de software, soy una persona muy analítica, y que le gustar obtener muchos conocimientos sobre la tecnología en general.
                Soy capaz de adaptarme a cualquier tipo de proyecto o trabajo relacionado a la tecnología, también puedo analizar los pequeños detalles de todos los proyectos que este desarrollando, por lo que me gusta enfocarme en uno solamente.
              </p>
            </Card>

            <div className="cards-profile">

              <Card className="card-competitions" >
                <h3>Mis competncias</h3>
                <p></p>
              </Card>

              <Card className="card-objective" >
                <h3>Mi Obtevio Profecional</h3>
                <p></p>
              </Card>

            </div>

          </div>

        </section>
    )
}
